.add-bookmark-section-control {
    margin-bottom: 10px;
    width: 100%;
    border-radius: 5px;
}

.add-bookmark-input-form {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}

.add-bookmark-name-input {
    background: rgb(255,255, 255, 0.5);
    width: 100%;
    color: black;
    border-radius: 5px;
    font-size: 16px;
    line-height: 24px;
}

.add-bookmark-name-input::-webkit-input-placeholder {
    color: black;
}

.add-bookmark-name-input::-ms-input-placeholder {
    color: black;
}

.add-bookmark-name-input::placeholder {
    color: black;
}

.add-bookmark-name-input-flex-container {
    -webkit-flex-grow: 4;
            flex-grow: 4;
}

.add-bookmark-button-flex-container {
    margin-left: 15px;
}

.add-bookmark-button {
    background: rgb(0,0,0, 0.5);
    border: 0;
    color: white;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
    line-height: 30px;
    padding-left: 9px;
    padding-right: 9px;
}

.add-bookmark-button:hover {
    background: rgb(0,0,0, 0.7);
}

.add-bookmark-button:disabled {
    background: rgb(0,0,0, 0.2);
    color: rgb(0,0,0, 0.2);
    cursor: not-allowed;
}

.bookmark-section h3 {
    margin-top: 0;
    margin-bottom: 0;
    color: white;
    line-height: 30px;
}

button.close-button {
    background: rgb(0,0,0, 0.5);
    border: none;
    font-size: 16px;
    cursor: pointer;
    color: white;
    padding-left: 7px;
    padding-right: 7px;
    border-radius: 5px;
    height: 30px;
}

button.close-button:hover {
    background: rgb(0,0,0, 0.70);
}

